<template>
    <div class="page" :style="'height:'+winHeight+'px'">
        <MyHeader>
            <div>系统消息</div>
        </MyHeader>
        <div class="content">
            <div class="no-message-box">
                <span class="no-message">暂无消息</span>
            </div>
        </div>
    </div>
</template>

<script>
import "lib-flexible"
import MyHeader from "./header";
export default {
    name:"Message",
    data:function(){
        return {
            winHeight:"",
            title:"message"
        }
    },
    beforeRouteEnter:function(to,from,next){
        next((vm)=>{
            vm.winHeight = window.innerHeight
        })
    },
    components:{MyHeader}
}
</script>

<style scoped>
.page{
    width:100%;
    display: flex;
    flex-direction: column;
    background: #f6f6f6;
}
.content{
    flex-grow: 1;
    display: flex;
    justify-content:center;
    flex-direction:column;
    background: #f6f6f6;
}
.no-message-box{
    text-align: center;
}
.no-message{
    display: inline-block;
    padding-top: 180px;
    width: 150px;
    color: #e2e2e2;
    font-size: 24px;
    background: url(./images/icon_no_message.png) no-repeat center top;
    background-size: 150px;
    
}
</style>




